/* Resume Page css */
.resume-right-col {
  background-color: rgb(93, 121, 37);
  color: white;
  padding: 2em;
}

/* contact page css */

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color:  olivedrab;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-right: 5rem;
}

/* contact page css */
.avatar-img {
  height: 250px;
  padding-top: 5em;
  border-radius: 50%;
}

.banner-text {
  background-color: rgb(10, 10, 126);
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

hr {
  color: aliceblue;
}

.progressbar 
.bar {
  background-color: aliceblue;
}

.progressbar.bar.bar1 {
  background: white;
}

.footer-main-page  {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.global-footer {
  background-color: rgb(10, 10, 126);
  width: 100%;
  padding: 50px;
}

.social-media {
  color: #fff;
  margin-left: 2px;
}